.signin-outer {
  height: auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-image: url("../../../assets/img/background.jpg");
}

.card-signin {
  border-radius: 40px;
  width: 100%;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 390px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  background-color: #ffffffbf;
  padding: 50px 8px !important;
}

.btn {
  border-radius: 7px;
  height: 38px;
  width: 100%;
  background-color: #fd6400;
  box-shadow: none;
}

.card-body {
  margin: 0 auto;
  width: 90%;
}

.select-lang {
  float: right;
  width: 200px;
  margin-top: 16px;
  margin-right: 21px;
}

a {
  color: #3e3e3e;
  font-weight: 500;
}

.signup a {
  color: #407ec9;
}

.w-75 {
  width: 480px !important;
}

.info {
  color: #3e3e3e;
  margin-top: 16px;
  font-size: 14px;
}

.btn1 {
  color: #000;
  background-color: #fff;
  margin: 0 10px 0 0;
  border: solid 0.4px #979797 !important;
}

@media (max-width: 1025px) {
  .card-signin {
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }
}

@media only screen and (max-width: 550px) {
  .card-signin {
    border-radius: 40px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }

  .w-75 {
    width: 100% !important;
  }

  .card-body {
    margin: 0 auto;
    width: 90%;
  }
}

.red {
  color: red;
}

.green {
  color: green;
}

.sublist {
  margin-left: 34px;
}

.sublist>span {
  min-width: 33px;
}

.term-wrapper {
  height: 500px;
  overflow-y: scroll;
  padding: 0 14px;
}

.heading-term {
  font-family: "GothamNarrowBold", sans-serif;
}

.sublist2 {
  margin-left: 82px;
}

b {
  font-family: "GothamNarrowBold", sans-serif;
}

@media screen and (max-width: 767px) {
  .card.card-signin.eula-part {
    padding: 20px 8px 50px 8px !important;
    background: #ffffff !important;
    border-radius: 1px !important;
    padding-top: 0 !important;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
  }

  .log-part {
    margin-bottom: 1rem;
  }

  .l-part {
    padding-bottom: 1rem;
  }

  .langPart h4 {
    font-size: 17px;
    font-weight: 600;
  }
}